<template>
    <div class="notice">
        <h4></h4>
        <ul class="list-head clearfix">
            <li class="table-li"></li>
            <li class="table-li"></li>
            <li class="table-li"></li>
        </ul>
        <ul class="notice-list">
            <li v-for="(notice,index) in noticeList">
                <ul class="clearfix">
                    <li class="pull-left table-li">{{ notice.goods }}</li>
                    <li class="pull-right table-li">
                        <a class="delete-btn" @click="deleteNotice(index)"></a>
                    </li>
                    <li class="pull-right table-li">{{ notice.time }}</li>
                </ul>
            </li>
        </ul>
    </div>
</template>
<script>
    export default {
        data() {
            return {
                noticeList: [
                    {
                        goods: 'Sony/ILCE-5100L A510016-50mm..',
                        time: '2017-05-30  16:21:45',
                    },
                    {
                        goods: 'Sony/ILCE-5100L A510016-50mm..',
                        time: '2017-05-30  16:21:45',
                    },
                    {
                        goods: 'Sony/ILCE-5100L A510016-50mm..',
                        time: '2017-05-30  16:21:45',
                    },
                ],
            };
        },
        methods: {
            deleteNotice(index) {
                this.noticeList.splice(index, 1);
            },
        },
    };
</script>


