                            <a class="order-btn submit-btn pull-left" @click="saveAddress"></a>
                            <a @click="cancelAdd"></a>
                        </div>
                    </div>
                </div>
            </div>
            <div class="address-selected self-take-select">
                <h5></h5>
                <div>
                    <label class="form-control-radio" v-for="take in selfTake">
                        <input type="radio" name="address">
                        <div class="address clearfix">
                            <p>
                                <span>{{ take.name }}</span>
                                <span>{{ take.phone }}</span>
                                <span class="address-detail">{{ take.address }}</span>
                                <a class="self-take pull-right"></a>
                            </p>
                        </div>
                    </label>
                </div>
                <a class="select-btn add-self-take"
                    @click="addSelfTake"
                    v-if="selfTake.length === 0">+</a>
                <modal ref="modal">
                    <div slot="title">
                        <h4 class="modal-title" v-text="modalTitle"></h4>
                    </div>
                    <div slot="body">
                        <form class="signup-form">
                            <div class="signup-form-group clearfix">
                                <label class="form-title"></label>
                                <input type="text"
                                       class="signup-form-control"
                                       name="username"
                                       placeholder=""
                                       v-model="newSelfTake.name">
                            </div>
                            <div class="signup-form-group clearfix">
                                <label class="form-title"></label>
                                <input type="number"
                                       class="signup-form-control"
                                       name="telphone"
                                       placeholder=""
                                       v-model.number="newSelfTake.phone">
                            </div>
                            <div class="signup-form-group clearfix">
                                <label class="form-title"></label>
                                <Cascader class="destination pull-left"
                                          :data="data"
                                          v-model="newSelfTake.address">
                                </Cascader>
                                <Select v-model="newSelfTake.city"  style="width:200px">
                                    <Option v-for="item in cityList"
                                            :value="item.value"
                                            :key="item">
                                        {{ item.label }}
                                    </Option>
                                </Select>
                            </div>
                        </form>
                    </div>
                    <button type="button"
                            class="order-btn"
                            @click="selfTakeAdd"
                            slot="save_address"></button>
                </modal>
            </div>
            <div class="pay-method">
                <h5 class="select-title"></h5>
                <div class="methods">
                    <label class="form-control-radio" v-for="method in methods">
                        <input type="radio" name="method">
                        <span>{{ method.name }}</span>
                    </label>
                </div>
            </div>
            <div class="pay-method invoice-info">
                <h5 class="select-title"></h5>
                <p>{{ invoice }} <a @click="modifyInvoice"></a></p>
                <modal ref="invoice">
                    <div slot="title">
                        <h4 class="modal-title"></h4>
                    </div>
                    <div slot="body">
                        <form class="signup-form">
                            <div class="signup-form-group clearfix">
                                <label class="form-title"></label>
                                <label class="form-control-radio">
                                    <input type="radio" name="invoice" value="" v-model="invoice">
                                    <span></span>
                                </label>
                                <label class="form-control-radio">
                                    <input type="radio" name="invoice" value="" v-model="invoice">
                                    <span></span>
                                </label>
                            </div>
                            <div class="signup-form-group clearfix">
                                <label class="form-title"></label>
                                <Select v-model="invoice.title" class="invoice-select" style="width:200px">
                                    <Option v-for="item in cityList"
                                            :value="item.value"
                                            :key="item">
                                        {{ item.label }}
                                    </Option>
                                </Select>
                            </div>
                            <div class="signup-form-group clearfix">
                                <label class="form-title"></label>
                                <input class="form-control invoice-content" type="text" v-model="invoice.info">
                            </div>
                        </form>
                    </div>
                    <button type="button"
                            @click="closeInvoice"
                            class="order-btn"
                            slot="save_address"></button>
                    <button type="button"
                            @click="notNeedInvoice"
                            class="order-btn notNeed"
                            slot="save_address"></button>
                </modal>
            </div>
            <div class="ensure-information">
                <p class="select-title"></p>
                <ul class="product-head clearfix">
                    <li class="pull-left"></li>
                    <li class="pull-left text-center">()</li>
                    <li class="pull-left text-center"></li>
                    <li class="pull-left text-center"></li>
                </ul>
                <ul class="product-list">
                    <li v-for="order in submitOrder.productList">
                        <h5>{{ order.shop }}</h5>
                        <ul class="order-detail clearfix">
                            <li class="pull-left clearfix">
                                <img class="pull-left" :src="order.img" alt="">
                                <div class="pull-left">
                                    <p>{{ order.name }}</p>
                                    <p>: {{ order.color }} : {{ order.size }}</p>
                                </div>
                            </li>
                            <li class="pull-left text-center">{{ order.price }}</li>
                            <li class="pull-left text-center">{{ order.num }}</li>
                            <li class="pull-left text-center">{{ order.price * order.num }}</li>
                        </ul>
                        <div>
                             <input class="form-control"
                                         type="text"
                                         placeholder="50">
                        </div>
                    </li>
                </ul>
                <p class="select-title">/</p>
                <ul class="select-offer clearfix">
                    <li class="pull-left">
                        <label class="form-control-radio">
                            <input type="radio" name="method">
                            <span></span>
                        </label>
                    </li>
                    <li class="pull-left">
                        <label class="form-control-radio">
                            <input type="radio" name="method">
                            <span></span>
                        </label>
                    </li>
                </ul>
                <div class="tab-content">
                    <ul class="tab-pane fade in active clearfix" :class="{used:used}" >
                        <li class="pull-left pane pull-left" v-for="coupon in coupons">
                            <div class="coupons">
                                <p class="text-center"><span class="symbol"></span>{{ coupon.money }}&nbsp;<span>{{ coupon.use }}</span></p>
                            </div>
                            <div class="coupons-info text-center">
                                <ul class="text-left">
                                    <li>{{ coupon.type }}</li>
                                    <li>{{ coupon.startTime }}-{{ coupon.endTime }}</li>
                                </ul>
                            </div>
                        </li>
                    </ul>
                </div>
                <div class="order-submit submit-btn">
                    <div class="order-submit-content clearfix">
                        <span class="order-price">-&yen;{{ submitOrder.freight }}</span>
                        <span class="name"></span>
                    </div>
                    <div class="order-submit-content clearfix">
                        <span class="order-price price">&yen;{{ total_price}}</span>
                        <span class="name">()</span>
                    </div>
                    <router-link to="/mall/order-success" class="order-btn submit-btn"></router-link>
                </div>
            </div>
        </div>
        <right-side></right-side>
    </div>
</template>

<script>
    import Cascader from 'iview/src/components/cascader';
    import { Select, Option, OptionGroup } from 'iview/src/components/select';
    import Modal from '../components/Modal.vue';
    import order from '../assets/images/details/order.png';
    import RightSide from '../layouts/RightSide.vue';

    export default {
        components: {
            Cascader,
            Modal,
            Option,
            OptionGroup,
            RightSide,
            Select,
        },
        computed: {
            total_price() {
                let totalPrice = 0;
                this.submitOrder.productList.forEach(item => {
                    totalPrice += item.price * item.num;
                });
                return totalPrice.toFixed(2);
            },
        },
        data() {
            return {
                address: {
                    name: '',
                    phone: '',
                    area: [],
                    detail: '',
                    isdefault: false,
                },
                addStatus: 1,
                addressSelect: [
                    {
                        address: '       1604',
                        isdefault: true,
                        name: '',
                        phone: 12345676543,
                    },
                    {
                        address: '       1604',
                        isdefault: false,
                        name: '',
                        phone: 12345676543,
                    },
                ],
                cityList: [
                    {
                        value: 'beijing',
                        label: '',
                    },
                    {
                        value: 'shanghai',
                        label: '',
                    },
                    {
                        value: 'shenzhen',
                        label: '',
                    },
                    {
                        value: 'hangzhou',
                        label: '',
                    },
                    {
                        value: 'nanjing',
                        label: '',
                    },
                    {
                        value: 'chongqing',
                        label: '',
                    },
                ],
                coupons: [
                    {
                        endTime: '2017.12.1',
                        money: 50.00,
                        other: '',
                        startTime: '5016.12.12',
                        type: '',
                        use: '399',
                    },
                    {
                        endTime: '2017.12.1',
                        money: 50.00,
                        other: '',
                        startTime: '5016.12.12',
                        type: '',
                        use: '399',
                    },
                ],
                model1: '',
                data: [
                    {
                        children: [
                            {
                                label: '',
                                value: 'gugong',
                            },
                            {
                                label: '',
                                value: 'tiantan',
                            },
                            {
                                label: '',
                                value: 'wangfujing',
                            },
                        ],
                        label: '',
                        value: 'beijing',
                    },
                    {
                        children: [
                            {
                                value: 'nanjing',
                                label: '',
                                children: [
                                    {
                                        value: 'fuzimiao',
                                        label: '',
                                    },
                                ],
                            },
                            {
                                value: 'suzhou',
                                label: '',
                                children: [
                                    {
                                        value: 'zhuozhengyuan',
                                        label: '',
                                    },
                                    {
                                        value: 'shizilin',
                                        label: '',
                                    },
                                ],
                            },
                        ],
                        label: '',
                        value: 'jiangsu',
                    },
                ],
                invoice: '',
                invoices: [
                    {
                        type: '',
                    },
                    {
                        type: '',
                    },
                ],
                methods: [
                    {
                        name: '',
                    },
                    {
                        name: '',
                    },
                ],
                modalTitle: '',
                newSelfTake: {
                    address: [],
                    city: '',
                    name: '',
                    phone: '',
                },
                selfTake: [
//                    {
//                        address: '       1604',
//                        name: '',
//                        phone: 12345676543,
//                    },
                ],
                submitOrder: {
                    integral_num: 1660,
                    integral_price: 16.6,
                    freight: 20,
                    productList: [
                        {
                            color: '',
                            img: order,
                            name: 'Purrfect diary 1-7555',
                            num: 2,
                            price: 39.9,
                            size: 'L',
                            shop: 'XXX',
                        },
                        {
                            color: '',
                            img: order,
                            name: 'Purrfect diary 1-7555',
                            num: 2,
                            price: 126.07,
                            size: 'M',
                            shop: 'XXX',
                        },
                    ],
                },
            };
        },
        methods: {
            addAddress() {
                this.addStatus = 2;
            },
            addSelfTake() {
                this.$refs.modal.open();
                this.modalTitle = '';
            },
            cancelAdd() {
                this.addStatus = 1;
            },
            editDefault(item) {
                this.addressSelect.forEach(address => {
                    address.isdefault = false;
                });
                item.isdefault = true;
            },
            modifyInvoice() {
                this.$refs.invoice.open();
            },
            closeInvoice() {
                this.$refs.invoice.close();
            },
            notNeedInvoice() {
                this.invoice = '';
                this.closeInvoice();
            },
            selfTakeAdd() {
                this.selfTake.push(this.newSelfTake);
                this.$refs.modal.close();
            },
            saveAddress() {
                this.addressSelect.push(this.address);
                this.addStatus = 1;
            },
        },
    };
</script>


